	module.exports = {"data":{"post":{"html":"<p>The <a href=\"http://www.shopblogger.de/blog/\">Shopblogger</a> writes about the trials and tribulations of running a supermarket in Germany and is one of the more popular German blogs. Today he explains about a <a href=\"http://www.shopblogger.de/blog/archives/4651-Gerichtstermin.html\">hearing in employment court</a> where basically an ex-employee was awarded back pay and vacation for hours she never worked. German employment law can be absurd (firing an employee is technically illegal, you can always be taken to court), and is the main reason MamaMaus no longer has a blog of her own (yes, she used to) and no longer wants to be mentioned here by name.</p>","fields":{"title":"Court Appointment","date":"16 Jan 2007","url":"https://papascott.de/2007-01-16-court-appointment/"}}},"pathContext":{"slug":"/2007-01-16-court-appointment/","prev":{"fields":{"title":"Support Our Son's Habit","date":"12 Jan 2007","slug":"/2007-01-12-support-our-sons-habit/"}},"next":{"fields":{"title":"Wii Don't","date":"16 Jan 2007","slug":"/2007-01-16-wii-dont/"}}}}

